@import url("https://fonts.googleapis.com/css2?family=Josefin+Sans:wght@100;300;400;600;700&family=Raleway:wght@100;300;600&display=swap");
@tailwind base;
@tailwind components;
@tailwind utilities;
:root {
  --header-height: 3.5rem;
  /*========== Colors ==========*/
  /*Color mode HSL(hue, saturation, lightness)*/
  --title-color: hsl(207, 4%, 16%);
  --text-color: hsl(207, 4%, 28%);
  --text-color-light: hsl(207, 4%, 56%);
  --body-color: hsl(207, 4%, 99%);
  --container-color: hsl(207, 4%, 95%);
  /*========== Font and typography ==========*/
  /*.5rem = 8px | 1rem = 16px ...*/
  --body-font: "Roboto", sans-serif;
  --title-font: "Lora", serif;
  --biggest-font-size: 1.5rem;
  --h1-font-size: 1.5rem;
  --h2-font-size: 1.25rem;
  --h3-font-size: 1rem;
  --normal-font-size: .938rem;
  --small-font-size: .813rem;
  --smaller-font-size: .75rem;
  --tiny-font-size: .625rem;
  /*========== Font weight ==========*/
  --font-medium: 500;
  --font-semi-bold: 600;
  /*========== z index ==========*/
  --z-normal: 1;
  --z-tooltip: 10;
  --z-fixed: 100;
}

::-webkit-scrollbar {
  display: none;
}

* {
  font-family: "Josefin Sans", sans-serif;
  box-sizing: border-box;
  text-decoration: none;
  padding: 0;
  margin: 0;
}

html,
body {
  scroll-behavior: smooth;
}

.container {
  width: 75%;
  margin: 4rem auto;
}

h1,
h2,
h3,
h4,
h5 {
  font-weight: 500;
}

h1 {
  font-size: 2.5rem;
}

.cta {
  display: inline-block;
  align-items: center;
  justify-content: center;
  color: var(--third-color);
  padding: 10px 40px;
  border: 3px solid var(--third-color);
  border-radius: 40px;
  transition: 1s;
}
.cta:hover {
  box-shadow: 0 0 25px var(--second-color), 0 0 25px var(--third-color);
}/*# sourceMappingURL=index.css.map */